.loader {
    background-color: rgba(0, 0, 0, 0.8);
    top:0;
    bottom:0;
    left:0;
    right:0;
    position: fixed;
    width: 100%;
    height: 100%;
}

.spinner {
    margin: auto;
    margin-top: 200px;
    width: 200px;
    height: 200px;
}

.spinner svg {
    animation: rotate 1.5s linear infinite;
    height: 100%;
    width: 100%;
}

.spinner circle {
    stroke: white;
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
    animation: dash 1.5s ease-in-out infinite 0s;
    stroke-linecap: round;
    fill: none;
    stroke-width: 3;
}

@keyframes rotate {
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes dash {
    0% {
        stroke-dasharray: 1, 200;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -35;
    }
    100% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -124;
    }
}

